import { translate } from '../../../utils/lang/i18n';
Blockly.Blocks.text_print = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('print %1'),
      args0: [{
        type: 'input_value',
        name: 'TEXT'
      }],
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      previousStatement: null,
      nextStatement: null,
      tooltip: translate('Text Print Tooltip'),
      category: Blockly.Categories.Text
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Text Print'),
      'description': translate('Text Print Description')
    };
  }
};

Blockly.JavaScript.text_print = function (block) {
  var msg = Blockly.JavaScript.valueToCode(block, 'TEXT', Blockly.JavaScript.ORDER_NONE) || '\'\'';
  var code = "window.alert(" + msg + ");\n";
  return code;
};