import { translate } from '../../../utils/lang/i18n';
Blockly.Blocks.text_changeCase = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('to %1 %2'),
      args0: [{
        type: 'field_dropdown',
        name: 'CASE',
        options: [[translate('UPPER CASE'), 'UPPERCASE'], [translate('lower case'), 'LOWERCASE'], [translate('Title Case'), 'TITLECASE']]
      }, {
        type: 'input_value',
        name: 'TEXT'
      }],
      output: 'String',
      outputShape: Blockly.OUTPUT_SHAPE_SQUARE,
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('Text Change Case Tooltip'),
      category: Blockly.Categories.Text
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Text Change Case'),
      'description': translate('Text Change Case Description')
    };
  }
};

Blockly.JavaScript.text_changeCase = function (block) {
  var operators = {
    UPPERCASE: '.toUpperCase()',
    LOWERCASE: '.toLowerCase()',
    TITLECASE: null
  };
  var operator = operators[block.getFieldValue('CASE')];
  var textOrder = operator ? Blockly.JavaScript.ORDER_MEMBER : Blockly.JavaScript.ORDER_NONE;
  var text = Blockly.JavaScript.valueToCode(block, 'TEXT', textOrder) || '\'\'';
  var code;

  if (operator) {
    code = "" + text + operator;
  } else {
    // eslint-disable-next-line no-underscore-dangle
    var functionName = Blockly.JavaScript.provideFunction_('textToTitleCase', [// eslint-disable-next-line no-underscore-dangle
    "function " + Blockly.JavaScript.FUNCTION_NAME_PLACEHOLDER_ + "(str) {\n                return str.toLowerCase().split(' ').map(function(word) {\n                    return word.replace(word[0], word[0].toUpperCase());\n                }).join(' ');\n            }"]);
    code = functionName + "(" + text + ")";
  }

  return [code, Blockly.JavaScript.ORDER_FUNCTION_CALL];
};