import { translate } from '../../../utils/lang/i18n';
Blockly.Blocks.math_arithmetic = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: '%1 %2 %3',
      args0: [{
        type: 'input_value',
        name: 'A',
        check: 'Number'
      }, {
        type: 'field_dropdown',
        name: 'OP',
        options: [['+', 'ADD'], ['-', 'MINUS'], ['*', 'MULTIPLY'], ['/', 'DIVIDE'], ['^', 'POWER']]
      }, {
        type: 'input_value',
        name: 'B',
        check: 'Number'
      }],
      output: 'Number',
      outputShape: Blockly.OUTPUT_SHAPE_ROUND,
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('Mathematic Arithmetic (Add, minus, mutiply, divide, power)'),
      category: Blockly.Categories.Mathematical
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Mathematic Arithmetic'),
      'description': translate('Mathematic Arithmetic Description')
    };
  }
};

Blockly.JavaScript.math_arithmetic = function (block) {
  var operators = {
    ADD: ['+', Blockly.JavaScript.ORDER_ADDITION],
    MINUS: ['-', Blockly.JavaScript.ORDER_SUBTRACTION],
    MULTIPLY: ['*', Blockly.JavaScript.ORDER_MULTIPLICATION],
    DIVIDE: ['/', Blockly.JavaScript.ORDER_DIVISION],
    POWER: [null, Blockly.JavaScript.ORDER_COMMA] // Handle power separately.

  };
  var tuple = operators[block.getFieldValue('OP')];
  var operator = tuple[0];
  var order = tuple[1];
  var argument0 = Blockly.JavaScript.valueToCode(block, 'A', order) || '0';
  var argument1 = Blockly.JavaScript.valueToCode(block, 'B', order) || '0';
  var code; // Power in JavaScript requires a special case since it has no operator.

  if (!operator) {
    code = "Math.pow(" + argument0 + ", " + argument1 + ")";
    return [code, Blockly.JavaScript.ORDER_FUNCTION_CALL];
  }

  code = argument0 + " " + operator + " " + argument1;
  return [code, order];
};