import config from '../../../../../constants/const';
import { translate } from '../../../../../utils/lang/i18n';
Blockly.Blocks.notify = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('Notify %1 with sound: %2 %3'),
      args0: [{
        type: 'field_dropdown',
        name: 'NOTIFICATION_TYPE',
        options: config.lists.NOTIFICATION_TYPE
      }, {
        type: 'field_dropdown',
        name: 'NOTIFICATION_SOUND',
        options: config.lists.NOTIFICATION_SOUND
      }, {
        type: 'input_value',
        name: 'MESSAGE',
        check: null
      }],
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      previousStatement: null,
      nextStatement: null,
      tooltip: translate('Creates a notification'),
      cateogry: Blockly.Categories.Miscellaneous
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Pop out notify'),
      'description': translate('Pop out notify description')
    };
  }
};

Blockly.JavaScript.notify = function (block) {
  var notificationType = block.getFieldValue('NOTIFICATION_TYPE');
  var sound = block.getFieldValue('NOTIFICATION_SOUND');
  var message = Blockly.JavaScript.valueToCode(block, 'MESSAGE') || "\"" + translate('<empty message>') + "\"";
  var code = "Bot.notify({ className: '" + notificationType + "', message: " + message + ", sound: '" + sound + "'});\n";
  return code;
};