import { translate } from '../../../../../utils/lang/i18n';
Blockly.Blocks.timeout = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('%1 %2 Run after %3 second(s)'),
      args0: [{
        type: 'input_dummy'
      }, {
        type: 'input_statement',
        name: 'TIMEOUTSTACK'
      }, {
        type: 'input_value',
        name: 'SECONDS'
      }],
      colour: '#fef1cf',
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      previousStatement: null,
      nextStatement: null,
      tooltip: translate('Run the blocks inside every n seconds'),
      category: Blockly.Categories.Time
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Timeout'),
      'description': translate('Timeout Description')
    };
  },
  onchange: function onchange(event) {
    var _this = this;

    if (!this.workspace || this.isInFlyout || this.workspace.isDragging()) {
      return;
    }

    if (event.type === Blockly.Events.END_DRAG) {
      var allowedScopes = ['trade_definition', 'during_purchase', 'before_purchase', 'after_purchase', 'tick_analysis'];

      if (allowedScopes.some(function (scope) {
        return _this.isDescendantOf(scope);
      })) {
        if (this.disabled) {
          this.setDisabled(false);
        }
      } else if (!this.disabled) {
        this.setDisabled(true);
      }
    }
  }
};

Blockly.JavaScript.timeout = function (block) {
  var stack = Blockly.JavaScript.statementToCode(block, 'TIMEOUTSTACK');
  var seconds = Blockly.JavaScript.valueToCode(block, 'SECONDS', Blockly.JavaScript.ORDER_ATOMIC) || '1';
  var code = "sleep(" + seconds + ");\n" + stack + "\n";
  return code;
};