import config from '../../../../../constants/const';
import { translate } from '../../../../../utils/lang/i18n';
Blockly.Blocks.balance = {
  init: function init() {
    var _this = this;

    this.jsonInit(this.definition()); // Change shape based on selected type

    var balanceTypeField = this.getField('BALANCE_TYPE');
    balanceTypeField.setValidator(function (value) {
      if (value === 'STR') {
        _this.setOutputShape(Blockly.OUTPUT_SHAPE_SQUARE);

        _this.setOutput(true, 'String');
      } else if (value === 'NUM') {
        _this.setOutputShape(Blockly.OUTPUT_SHAPE_ROUND);

        _this.setOutput(true, 'Number');
      }

      _this.initSvg();

      _this.render(false);

      return undefined;
    });
  },
  definition: function definition() {
    return {
      message0: translate('Balance: %1'),
      args0: [{
        type: 'field_dropdown',
        name: 'BALANCE_TYPE',
        options: config.lists.BALANCE_TYPE
      }],
      output: null,
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('Balance Tooltip'),
      category: Blockly.Categories.Miscellaneous
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Balance'),
      'description': translate('Balance Description')
    };
  }
};

Blockly.JavaScript.balance = function (block) {
  var balanceType = block.getFieldValue('BALANCE_TYPE');
  var code = "Bot.getBalance('" + balanceType + "')";
  return [code, Blockly.JavaScript.ORDER_ATOMIC];
};