import "core-js/modules/es6.regexp.match";
import "core-js/modules/es6.number.constructor";
import { translate } from '../../../../utils/lang/i18n';
Blockly.Blocks.controls_repeat_ext = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('repeat %1 times'),
      args0: [{
        type: 'input_value',
        name: 'TIMES',
        check: 'Number'
      }],
      message1: translate('do %1'),
      args1: [{
        type: 'input_statement',
        name: 'DO'
      }],
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      previousStatement: null,
      nextStatement: null,
      tooltip: translate('Control Repeat Tooltip'),
      category: Blockly.Categories.Loop
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Control Repeat'),
      'description': translate('Control Repeat Description')
    };
  }
};

Blockly.JavaScript.controls_repeat_ext = function (block) {
  var repeats;

  if (block.getField('TIMES')) {
    repeats = String(Number(block.getFieldValue('TIMES')));
  } else {
    repeats = Blockly.JavaScript.valueToCode(block, 'TIMES') || '0';
  }

  var branch = Blockly.JavaScript.statementToCode(block, 'DO');
  var code = ''; // eslint-disable-next-line no-underscore-dangle

  var loopVar = Blockly.JavaScript.variableDB_.getDistinctName('count', Blockly.Variables.NAME_TYPE);
  var endVar = repeats;

  if (!repeats.match(/^\w+$/) && !Blockly.isNumber(repeats)) {
    // eslint-disable-next-line no-underscore-dangle
    endVar = Blockly.JavaScript.variableDB_.getDistinctName('repeat_end', Blockly.Variables.NAME_TYPE);
    code += "var " + endVar + " = " + repeats + ";\n";
  }

  code += "\n    for (var " + loopVar + " = 0; " + loopVar + " < " + endVar + "; " + loopVar + "++) {\n        " + branch + "\n    }\n";
  return code;
};