import { translate } from '../../../../utils/lang/i18n';
Blockly.Blocks.lists_repeat = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('set %1 to item %2 repeated %3 times'),
      args0: [{
        type: 'field_variable',
        name: 'VARIABLE',
        variable: translate('list')
      }, {
        type: 'input_value',
        name: 'ITEM'
      }, {
        type: 'input_value',
        name: 'NUM'
      }],
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      previousStatement: null,
      nextStatement: null,
      tooltip: translate('List Repeat Tooltip'),
      category: Blockly.Categories.List
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('List Repeat'),
      'description': translate('List Repeat Description')
    };
  }
};

Blockly.JavaScript.lists_repeat = function (block) {
  // eslint-disable-next-line no-underscore-dangle
  var varName = Blockly.JavaScript.variableDB_.getName(block.getFieldValue('VARIABLE'), Blockly.Variables.NAME_TYPE); // eslint-disable-next-line no-underscore-dangle

  var functionName = Blockly.JavaScript.provideFunction_('listsRepeat', [// eslint-disable-next-line no-underscore-dangle
  "function " + Blockly.JavaScript.FUNCTION_NAME_PLACEHOLDER_ + "(value, n) {\n            var array = [];\n            for (var i = 0; i < n; i++) {\n                array[i] = value;\n            }\n            return array;\n        }"]);
  var element = Blockly.JavaScript.valueToCode(block, 'ITEM', Blockly.JavaScript.ORDER_COMMA) || 'null';
  var repeatCount = Blockly.JavaScript.valueToCode(block, 'NUM', Blockly.JavaScript.ORDER_COMMA) || '0';
  var code = varName + " = " + functionName + "(" + element + ", " + repeatCount + ");\n";
  return code;
};