import "core-js/modules/es7.array.includes";
import "core-js/modules/es6.string.includes";
import { expectValue } from '../../../shared';
import config from '../../../../constants/const';
import { translate } from '../../../../utils/lang/i18n';
Blockly.Blocks.bb_statement = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('set %1 to Bollinger Bands %2 %3'),
      message1: '%1',
      args0: [{
        type: 'field_variable',
        name: 'VARIABLE',
        variable: 'bb'
      }, {
        type: 'field_dropdown',
        name: 'BBRESULT_LIST',
        options: config.bbResult
      }, {
        type: 'input_dummy'
      }],
      args1: [{
        type: 'input_statement',
        name: 'STATEMENT',
        check: null
      }],
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('Calculates Bollinger Bands (BB) from a list with a period'),
      previousStatement: null,
      nextStatement: null,
      category: Blockly.Categories.Indicators
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('BB Statement'),
      'descriptionL': translate('BB Statement Description')
    };
  },
  onchange: function onchange(event) {
    var _this = this;

    if (!this.workspace || this.isInFlyout || this.workspace.isDragging()) {
      return;
    }

    if (event.type === Blockly.Events.END_DRAG) {
      var blocksInStatement = this.getBlocksInStatement('STATEMENT');
      blocksInStatement.forEach(function (block) {
        if (!_this.requiredParamBlocks.includes(block.type)) {
          Blockly.Events.disable();
          block.unplug(false);
          Blockly.Events.enable();
        }
      });
    }
  },
  requiredParamBlocks: ['input_list', 'period', 'std_dev_multiplier_up', 'std_dev_multiplier_down']
};

Blockly.JavaScript.bb_statement = function (block) {
  // eslint-disable-next-line no-underscore-dangle
  var varName = Blockly.JavaScript.variableDB_.getName(block.getFieldValue('VARIABLE'), Blockly.Variables.NAME_TYPE);
  var bbResult = block.getFieldValue('BBRESULT_LIST');
  var input = expectValue(block.getChildByType('input_list'), 'INPUT_LIST');
  var period = block.childValueToCode('period', 'PERIOD') || '10';
  var stdDevUp = block.childValueToCode('std_dev_multiplier_up', 'UPMULTIPLIER') || '5';
  var stdDevDown = block.childValueToCode('std_dev_multiplier_down', 'DOWNMULTIPLIER') || '5';
  var code = varName + " = Bot.bb(" + input + ", { periods: " + period + ", stdDevUp: " + stdDevUp + ", stdDevDown: " + stdDevDown + " }, " + bbResult + ");\n";
  return code;
};