import { translate } from '../../../../utils/lang/i18n';
Blockly.Blocks.lists_sort = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: translate('sort %1 %2 %3'),
      args0: [{
        type: 'field_dropdown',
        name: 'TYPE',
        options: [[translate('numeric'), 'NUMERIC'], [translate('alphabetic'), 'TEXT']]
      }, {
        type: 'field_dropdown',
        name: 'DIRECTION',
        options: [[translate('ascending'), '1'], [translate('descending'), '-1']]
      }, {
        type: 'input_value',
        name: 'LIST'
      }],
      output: 'Array',
      outputShape: Blockly.OUTPUT_SHAPE_ROUND,
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('List Sort Tooltip'),
      category: Blockly.Categories.List
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('List Sort'),
      'description': translate('List Sort Description')
    };
  }
};

Blockly.JavaScript.lists_sort = function (block) {
  var list = Blockly.JavaScript.valueToCode(block, 'LIST', Blockly.JavaScript.ORDER_FUNCTION_CALL) || '[]';
  var direction = block.getFieldValue('DIRECTION') === '1' ? 1 : -1;
  var type = block.getFieldValue('TYPE'); // eslint-disable-next-line no-underscore-dangle

  var getCompareFunctionName = Blockly.JavaScript.provideFunction_('listsGetSortCompare', [// eslint-disable-next-line no-underscore-dangle
  "function " + Blockly.JavaScript.FUNCTION_NAME_PLACEHOLDER_ + "(type, direction) {\n            var compareFuncs = {\n                NUMERIC: function(a, b) {\n                    return parseFloat(a) - parseFloat(b);\n                },\n                TEXT: function(a, b) {\n                    return a.toString().toLowerCase() > b.toString().toLowerCase() ? 1 : -1;\n                }\n            };\n\n            return function(a, b) {\n                return compareFuncs[type](a, b) * direction; \n            }\n        }"]);
  var code = list + ".slice(0).sort(" + getCompareFunctionName + "(\"" + type + "\", " + direction + "))";
  return [code, Blockly.JavaScript.ORDER_FUNCTION_CALL];
};