import { translate } from '../../../utils/lang/i18n';
Blockly.Blocks.math_constant = {
  init: function init() {
    this.jsonInit(this.definition());
  },
  definition: function definition() {
    return {
      message0: '%1',
      args0: [{
        type: 'field_dropdown',
        name: 'CONSTANT',
        options: [["\u03C0", 'PI'], ["\u2107", 'E'], ["\u03D5", 'GOLDEN_RATIO'], ['sqrt(2)', 'SQRT2'], ["sqrt(\xBD)", 'SQRT1_2'], ["\u221E", 'INFINITY']]
      }],
      output: 'Number',
      outputShape: Blockly.OUTPUT_SHAPE_ROUND,
      colour: Blockly.Colours.Binary.colour,
      colourSecondary: Blockly.Colours.Binary.colourSecondary,
      colourTertiary: Blockly.Colours.Binary.colourTertiary,
      tooltip: translate('Math Constant Tooltip'),
      category: Blockly.Categories.Mathematical
    };
  },
  meta: function meta() {
    return {
      'display_name': translate('Math Constant'),
      'description': translate('Math Constant Description')
    };
  }
};

Blockly.JavaScript.math_constant = function (block) {
  var constant = block.getFieldValue('CONSTANT');
  var code, order;

  if (constant === 'PI') {
    code = 'Math.PI';
    order = Blockly.JavaScript.ORDER_MEMBER;
  } else if (constant === 'E') {
    code = 'Math.E';
    order = Blockly.JavaScript.ORDER_MEMBER;
  } else if (constant === 'GOLDEN_RATIO') {
    code = '(1 + Math.sqrt(5)) / 2';
    order = Blockly.JavaScript.ORDER_DIVISION;
  } else if (constant === 'SQRT2') {
    code = 'Math.SQRT2';
    order = Blockly.JavaScript.ORDER_MEMBER;
  } else if (constant === 'SQRT1_2') {
    code = 'Math.SQRT1_2';
    order = Blockly.JavaScript.ORDER_MEMBER;
  } else if (constant === 'INFINITY') {
    code = 'Infinity';
    order = Blockly.JavaScript.ORDER_ATOMIC;
  }

  return [code, order];
};